import React, { useContext, useState } from 'react';

import Modal from '../UI/Modal';
import CartItem from './CartItem';
import classes from './Cart.module.css';
import CartContext from '../../store/cart-context';
import Checkout from './Checkout'
import apiPost from '../../api'

const Cart = (props) => {
	const cartCtx = useContext(CartContext);
	const [isCheckout, setIsCheckout] = useState(false);
	const [isSubmitting, setIsSubmitting] = useState(false);
	const [didSubmit, setDidSubmit] = useState(false)

	const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
	const hasItems = cartCtx.items.length > 0;


	const cartItemRemoveHandler = (id) => { cartCtx.removeItem(id);};
	const cartItemAddHandler = (item) => { cartCtx.addItem(item); };
	const orderHandler = () =>{ setIsCheckout(true) };

	const submitOrderHandler = async (userData) => {
		setIsSubmitting(true);

		await fetch(apiPost, {
			method: 'POST',
			body: JSON.stringify({
				user: userData,
				orderedItems: cartCtx.items
			})
		});//not umplementing error handling for now

		setIsSubmitting(false)
		setDidSubmit(true)
		cartCtx.clearCart();
	};



	const cartItems = (
		<ul className={classes['cart-items']}>
			{cartCtx.items.map((item) => (
				<CartItem
					key={item.id}
					name={item.name}
					amount={item.amount}
					price={item.price}
					onRemove={cartItemRemoveHandler.bind(null, item.id)}
					onAdd={cartItemAddHandler.bind(null, item)}
				/>
			))}
		</ul>
	);


	const modalButtons = (
		<div className={classes.actions}>
			<button className={classes['button--alt']} onClick={props.onClose}>Close</button>
			{hasItems && <button className={classes.button} onClick={orderHandler}>Order</button>}
		</div>
	);

	const cartModalContent = (
		<React.Fragment>
			{cartItems}
			<div className={classes.total}>
				<span>Total Amount</span>
				<span>{totalAmount}</span>
			</div>
		
			{!isCheckout && modalButtons}
			{isCheckout && <Checkout onConfirm={submitOrderHandler} onCancel={props.onClose}/>}
		</React.Fragment>
	);

	const isSubmittingContent = <p>Sending order...</p>;

	const didSubmitContent = (
		<React.Fragment>
			<p>Order sent successfully!</p>
			<div className={classes.actions}>
				<button className={classes.button} onClick={props.onClose}>Close</button>
			</div>
		</React.Fragment>
	)



	return (
		<Modal onClose={props.onClose}>
		{!isSubmitting && !didSubmit && cartModalContent}
		{isSubmitting && isSubmittingContent}
		{!isSubmitting && didSubmit && didSubmitContent}
		</Modal>
	);
};

export default Cart;
